@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,700);
@mixin f {
    font-family: 'Roboto', Arial, sans-serif; }

$blue: #0f518f;
$bluedark: #0e4a82;
$bg: #f5f7fa;
$border: #edeff2;
$cl: #333333;

html {
    height: 100%;
    box-sizing: border-box; }

*,
*:before,
*:after {
    box-sizing: inherit;
 }    // box-shadow: 0 0 0 1px red

body {
    min-width: 320px;
    @include f;
    font-size: 14px;
    font-weight: 300;
    color: $cl;
    line-height: 1.3;
    -webkit-font-smoothing: antialiased; }
button,
input,
textarea {
    background: none;
    border: none;
    outline: none; }
a {
    color: $bluedark;
    text-decoration: none; }

.center {
    max-width: 1650px;
    margin: 0 auto;
    padding: 0 120px;
    @include r(1279) {
        padding: 0 47px; }
    @include r(1023) {
        padding: 0 20px; }
    @include r(799) {
        padding: 0 10px; } }
.logo {
    font-size: 0;
    color: #FFF;
    user-select: none;
    cursor: default;
    &__icon,
    &__title {
        display: inline-block;
        vertical-align: middle; }
    &__icon {
        position: relative;
        margin-right: 20px;
        padding-right: 21px;
        &:after {
            content: '';
            position: absolute;
            top: 13px;
            right: 0;
            width: 1px;
            height: 30px;
            background: #FFF;
            @include r(1900) {
                top: 7px; }
            @include r(1023) {
                top: 4px;
                height: 24px; } }
        .icon {
            font-size: 56px;
            @include r(1900) {
                font-size: 44px; }
            @include r(1023) {
                font-size: 30px; } } }
    &__title {
        width: 160px;
        font-size: 14px;
        @include r(1900) {
            width: 140px;
            font-size: 12px; }
        @include r(1023) {
            font-size: 10px; } } }

.lang {
    position: relative;
    &__head {
        height: 30px;
        padding: 0 26px 0 13px;
        border: 1px solid rgba(white,.5);
        border-radius: 15px;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 28px;
        user-select: none;
        cursor: pointer;
        @include r(1023) {
            padding: 0 20px 0 8px;
            height: 24px;
            line-height: 24px; }
        &:after {
            content: '';
            position: absolute;
            top: 12px;
            right: 13px;
            @include arr(8,4,#fff,b);
            transition: transform .2s;
            @include r(1023) {
                top: 11px;
                right: 8px; } } }
    &__list {
        position: absolute;
        top: 30px;
        left: 0;
        visibility: hidden;
        opacity: 0;
        transition: opacity .2s, visibility .2s; }
    &__item {
        display: block;
        padding: 0 13px;
        text-transform: uppercase;
        line-height: 30px;
        color: #FFF; }
    &.active &__head {
        &:after {
            transform: rotate(-180deg); } }
    &.active &__list {
        visibility: visible;
        opacity: 1; } }

.title {
    margin-bottom: 80px;
    font-size: 42px;
    text-align: center;
    line-height: 1.1;
    @include r(1900) {
        margin-bottom: 70px;
        font-size: 30px; }
    &_sm {
        font-size: 32px;
        line-height: 1.2;
        @include r(1900) {
            font-size: 24px; } } }

.content {
    font-size: 16px;
    line-height: 1.5;
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p,
    ul,
    ol {
        &:first-child {
            margin-top: 0; } }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        font-weight: 400; }
    h2 {
        font-size: 22px;
        @include r(1900) {
            font-size: 20px; }
        @include r(1023) {
            font-size: 22px; } }
    h3 {
        font-size: 18px; }
    ul {
        padding-left: 20px; }
    li {
        margin-bottom: 5px; } }

.btn {
    height: 62px;
    padding: 0 50px;
    background: $bluedark;
    border-radius: 30px;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 700;
    color: #FFF;
    transition: background .2s;
    @include r(1900) {
        height: 52px;
        font-size: 15px; }
    @include r(1023) {
        height: 62px;
        font-size: 16px; }
    &:hover {
        background: $blue; } }

.social {
    font-size: 0;
    &__link {
        display: inline-block;
        margin-right: 20px;
        vertical-align: top;
        &:last-child {
            margin-right: 0; }
        &_instagram {
            color: #6a453a; }
        &_facebook {
            color: #3A5897; }
        &_vkontakte {
            color: #4D76A0; }
        &_youtube {
            color: #F05A4A; }
        .icon {
            font-size: 40px; } }
    &_sm &__link {
        margin-right: 10px;
        &:last-child {
            margin-right: 0; }
        .icon {
            font-size: 20px; } } }

.header {
    padding: 30px 0;
    color: #FFF;
    &__center {
        max-width: 1610px;
        @include clr; }
    &__right {
        float: right;
        padding-top: 13px;
        @include r(1900) {
            padding-top: 5px; }
        @include r(1023) {
            padding-top: 2px; } }
    &__lang {
        float: left;
        @include r(1023) {
            font-size: 10px; } }
    &__phone {
        float: left;
        margin-right: 30px;
        font-size: 18px;
        color: #FFF;
        line-height: 30px;
        @include r(1900) {
            font-size: 16px; }
        @include r(1023) {
            font-size: 13px;
            margin-right: 15px;
            line-height: 24px; }
        &-bold {
            font-weight: 700; } } }

.main {
    &__header {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        z-index: 4; }
    &__slider {
        position: relative;
        z-index: 2; } }

.mechanic {
    &__img {
        display: block;
        @include r(1900) {
            width: auto;
            max-height: 480px; } }
    &__profit {
        position: absolute;
        top: 110px;
        left: 325px;
        font-size: 0;
        @include r(1900) {
            top: 50px;
            left: 200px;
            &:after {
                content: none;
                display: none; } }
        &:after {
            content: '';
            position: absolute;
            top: 38px;
            left: 53px;
            width: 160px;
            height: 160px;
            background: url(../img/circles.png) no-repeat 0 0; }
        &-value {
            font-size: 142px;
            font-weight: 700;
            letter-spacing: -3px;
            vertical-align: top;
            line-height: 1;
            @include r(1900) {
                font-size: 84px; } }
        &-percent {
            font-size: 80px;
            font-weight: 700;
            vertical-align: top;
            @include r(1900) {
                font-size: 40px; } }
        &-title {
            font-size: 26px;
            font-weight: 300;
            @include r(1900) {
                font-size: 17px; } } }
    &__about {
        position: absolute;
        left: 330px;
        bottom: 90px;
        min-width: 380px;
        padding-left: 21px;
        border-left: 1px solid rgba(#fff,.7);
        @include r(1900) {
            left: 180px;
            bottom: 80px; }
        @include r(1023) {
            bottom: 130px; } }
    &__title {
        font-size: 26px;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 1;
        @include r(1900) {
            font-size: 20px; } }
    &__text {
        margin-top: 12px;
        font-size: 16px;
        @include r(1900) {
            font-size: 14px; }
        &_c {
            font-style: italic; } } }

.slider {
    &__list {
        position: relative;
        z-index: 2; }
    &__item {
        background-position: 50% 50%;
        background-size: cover;
        color: #FFF;
        @include r(1023) {
            overflow: hidden; } }
    &__center {
        position: relative;
        height: 998px;
        @include r(1900) {
            height: 650px; }
        @include r(1023) {
            height: 700px; } }
    &__mechanic {
        position: absolute;
        left: 0;
        bottom: 0;
        @include r(1900) {
            left: 120px; }
        @include r(1279) {
            left: 47px; }
        @include r(1023) {
            left: -60px; } }
    &__content {
        position: absolute;
        top: 285px;
        right: 0;
        max-width: 610px;
        @include r(1900) {
            top: 220px;
            right: 210px;
            max-width: 320px; }
        @include r(1279) {
            right: auto;
            left: 60%; }
        @include r(1023) {
            left: 58%; } }
    &__title {
        font-size: 60px;
        font-weight: 700;
        line-height: 1.15;
        @include r(1900) {
            font-size: 34px; } }
    &__text {
        margin-bottom: 70px;
        font-size: 46px;
        line-height: 1.35;
        @include r(1900) {
            margin-bottom: 40px;
            font-size: 24px; } }
    &__nav {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 124px;
        height: 0;
        text-align: right;
        z-index: 4;
        @include r(1023) {
            position: relative;
            left: 7px;
            bottom: 60px; }
        &-center {
            height: 0;
            @include r(1023) {
                text-align: center; } } }
    .slick-prev,
    .slick-dots,
    .slick-next {
        display: inline-block;
        font-size: 0;
        vertical-align: middle; }
    .slick-prev,
    .slick-next {
        padding: 10px;
        color: #FFF;
        .icon {
            font-size: 14px; } }
    .slick-arrow {
        @include r(1023) {
            display: none!important; } }
    .slick-dots {
        padding: 0;
        list-style: none;
        li {
            display: inline-block;
            vertical-align: middle; }
        button {
            width: 30px;
            height: 30px;
            &:before {
                content: '';
                display: inline-block;
                width: 10px;
                height: 10px;
                border: 1px solid rgba(#fff,.5);
                border-radius: 50%;
                vertical-align: middle;
                transition: all .2s;
                @include r(1023) {
                    width: 8px;
                    height: 8px; } } }
        .slick-active {
            button {
                &:before {
                    border-color: $bluedark;
                    background: $bluedark;
                    transform: scale(1.4);
                    @include r(1023) {
                        transform: scale(1.1); } } } } } }

.section {
    padding: 95px 0 100px;
    @include r(1900) {
        padding: 75px 0 70px; }
    @include r(1023) {
        padding: 70px 0 75px; }
    &__bg {
        position: absolute;
        font-size: 0;
        z-index: -1;
        &_blue {
            bottom: -340px;
            left: 50%;
            margin-left: -130px;
            transform: rotate(-44deg);
            color: $bluedark;
            @include r(1900) {
                bottom: -60px;
                left: 58%; }
            @include r(1279) {
                left: 65%;
                bottom: -65px; }
            @include r(1023) {
                left: 60%;
                bottom: -100px;
                transform: rotate(77deg); }
            .icon {
                font-size: 1440px;
                @include r(1900) {
                    font-size: 820px; }
                @include r(1023) {
                    font-size: 700px; } } }
        &_gray {
            bottom: -200px;
            right: 50%;
            margin-right: 300px;
            transform: rotate(165deg);
            color: #2F3338;
            @include r(1900) {
                right: 30%;
                bottom: -145px;
                transform: rotate(155deg); }
            @include r(1279) {
                right: 27%; }
            .icon {
                font-size: 820px;
                @include r(1900) {
                    font-size: 680px; }
                @include r(1279) {
                    font-size: 640px; } } } }
    &_dark {
        background: #2d3136;
        color: #fff;
        overflow: hidden; }
    &_bg {
        position: relative;
        overflow: hidden;
        z-index: 2; } }

.els {
    margin: 0 -15px 60px;
    font-size: 0;
    @include r(1023) {
        margin-left: -10px;
        margin-right: -10px; }
    &__col {
        display: inline-block;
        width: 33.33%;
        margin-bottom: 30px;
        padding: 0 15px;
        vertical-align: top;
        @include r(1023) {
            width: 50%;
            padding: 0 10px;
            margin-bottom: 20px; } }
    &__item {
        position: relative;
        background: $bg; }
    &__item.active &__over {
        visibility: visible;
        opacity: 1; }
    &__img {
        width: 100%; }
    &__info {
        padding: 5px 50px 70px;
        @include r(1023) {
            padding-bottom: 55px; } }
    &__title {
        margin-bottom: 15px;
        font-size: 22px;
        font-weight: 400;
        @include r(1900) {
            font-size: 20px; } }
    &__more {
        display: inline-block;
        &-title,
        &-icon {
            display: inline-block;
            vertical-align: middle; }
        &-title {
            font-size: 18px;
            @include r(1900) {
                font-size: 16px; } }
        &-icon {
            margin-left: 9px;
            padding-top: 2px;
            .icon {
                font-size: 7px; } } }
    &__over {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #2D3136;
        color: #fff;
        visibility: hidden;
        opacity: 0;
        transition: opacity .2s, visibility .2s;
        &-close {
            position: absolute;
            top: 0;
            right: 0;
            padding: 20px;
            font-size: 0;
            color: #fff;
            @include r(1023) {
                padding: 15px; }
            .icon {
                font-size: 16px;
                @include r(1023) {
                    font-size: 12px; } } }
        &-text {
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            padding: 50px 70px;
            font-size: 16px;
            @include r(1279) {
                padding: 55px 50px; }
            @include r(1023) {
                padding: 45px; } } } }

.benefits {
    margin-bottom: -60px;
    font-size: 0;
    @include r(1900) {
        margin-left: -15px;
        margin-right: -15px; }
    @include r(1023) {
        margin-bottom: -30px; }
    &__item {
        display: inline-block;
        width: 33.33%;
        margin-bottom: 60px;
        padding: 0 50px;
        vertical-align: middle;
        @include r(1900) {
            padding: 0 15px; }
        @include r(1023) {
            margin-bottom: 45px;
            padding-left: 25px;
            width: 50%;
            &:nth-child(2n+1) {
                padding-left: 70px; } } }
    &__icon,
    &__text {
        display: table-cell;
        vertical-align: middle; }
    &__icon {
        width: 80px;
        height: 80px;
        background: $bg;
        border-radius: 50%;
        @include vertical;
        color: $bluedark;
        .icon {
            font-size: 30px;
            vertical-align: middle; } }
    &__text {
        max-width: 206px;
        padding-left: 30px;
        font-size: 16px; } }

.control {
    margin: 0 -20px;
    .btn {
        @include r(1900) {
            margin-left: -72px;
            margin-right: -72px; }
        @include r(1279) {
            margin-left: 0;
            margin-right: 0;
            padding: 0 40px; }
        @include r(1023) {
            margin-left: -40px;
            padding: 0 60px; } }
    &__row {
        display: table;
        width: 100%;
        margin-bottom: 100px;
        @include r(1279) {
            margin-bottom: 75px; }
        @include r(1023) {
            margin-bottom: 50px; }
        &:last-child {
            margin: 0; } }
    &__col {
        display: table-cell;
        width: 50%;
        padding: 0 20px;
        vertical-align: middle;
        &_1 {
            @include r(1900) {
                padding: 0; }
            .control__img {
                @include r(1900) {
                    max-width: 105%; }
                @include r(1279) {
                    position: relative;
                    left: -5%;
                    max-width: 115%; }
                @include r(1023) {
                    left: -25%; } } }
        &_2 {
            .control__img {
                @include r(1279) {
                    position: relative;
                    left: -5%; }
                @include r(1023) {
                    max-width: 130%; } } }
        &_3 {
            .control__img {
                @include r(1279) {
                    position: relative;
                    left: -8%;
                    max-width: 110%; }
                @include r(1023) {
                    left: -45%;
                    max-width: 140%; } } }
        &_4 {
            .control__img {
                @include r(1279) {
                    position: relative;
                    left: -15%;
                    max-width: 115%; }
                @include r(1023) {
                    max-width: 155%;
                    left: -17%; } } }
        &_5 {
            .control__img {
                @include r(1279) {
                    position: relative;
                    left: -5%;
                    top: -50px;
                    max-width: 110%; }
                @include r(1023) {
                    left: -40%;
                    top: -80px;
                    max-width: 130%; } } } }
    &__img {
        max-width: 100%; }
    &__wrap {
        padding-left: 90px;
        @include r(1279) {
            padding-left: 25px; }
        @include r(1023) {
            padding-left: 0; } }
    &__btn {
        margin-top: 70px; }
    &__no-tablet {
        @include r(1279) {
            display: none; } } }

.action {
    margin: 0 -15px;
    font-size: 0;
    &__col {
        display: inline-block;
        width: 33.33%;
        padding: 0 15px;
        vertical-align: top;
        @include r(1023) {
            width: 100%;
            padding: 0 10px; }
        & + & {
            @include r(1023) {
                width: 50%; } }
        &_2 {
            @include r(1023) {
                .action__text {
                    width: 75%; }
                .action__item {
                    img {
                        width: 50%; } } } } }
    &__item {
        position: relative;
        background: $bg;
        @include r(1900) {
            overflow: hidden;
            height: 502px;
            img {
                display: block;
                margin-left: auto;
                margin-right: auto;
                width: 85%; } }
        @include r(1023) {
            height: auto;
            margin-bottom: 20px; } }
    &__img {
        width: 100%; }
    &__info {
        min-height: 230px;
        padding: 5px 50px 70px;
        @include r(1023) {
            padding-left: 45px;
            padding-right: 30px;
            padding-bottom: 10px; } }
    &__title {
        margin-bottom: 20px;
        font-size: 22px;
        font-weight: 400;
        @include r(1900) {
            margin-bottom: 10px;
            font-size: 20px; }
        @include r(1023) {
            font-size: 22px; } }
    &__text {
        margin-bottom: 10px;
        font-size: 16px;
        line-height: 1.5; }
    &__more {
        display: inline-block;
        &-title,
        &-icon {
            display: inline-block;
            vertical-align: middle; }
        &-title {
            font-size: 18px;
            @include r(1900) {
                font-size: 16px; } }
        &-icon {
            margin-left: 9px;
            padding-top: 2px;
            .icon {
                font-size: 14px;
                @include r(1900) {
                    font-size: 12px; }
                @include r(1279) {
                    position: relative;
                    top: 9px; }
                @include r(1023) {
                    top: -1px; } } } }
    &__break-tablet {
        @include r(1279) {
            display: block;
            width: 90%; }
        @include r(1023) {
            display: inline-block;
            width: auto; } } }

.res {
    padding: 50px 0 100px;
    @include r(1023) {
        position: relative; }
    .title {
        @include r(1900) {
            margin-bottom: 85px; }
        @include r(1279) {
            text-align: left; }
        @include r(1023) {
            position: absolute;
            left: 0;
            right: 0;
            top: -40px;
            z-index: 1;
            text-align: center; } }
    &__nav {
        position: relative;
        max-width: 570px;
        height: 0;
        margin: 0 0 -70px 50%;
        padding-right: 10px;
        text-align: right;
        z-index: 4;
        @include r(1900) {
            left: -70px; }
        @include r(1279) {
            left: -30px; }
        @include r(1023) {
            position: absolute;
            left: 0;
            right: 0;
            top: auto;
            bottom: 200px;
            margin: 0;
            text-align: center;
            max-width: 100%; } }
    &__list {
        z-index: 2; }
    &__item {
        padding-top: 50px; }
    &__row {
        margin-bottom: 100px;
        padding-bottom: 100px;
        border-bottom: 1px solid #272a2f;
        @include clr;
        @include r(1900) {
            margin-bottom: 0; }
        @include r(1023) {
            position: relative;
            padding-top: 110px; } }
    &__col {
        float: right;
        width: 50%;
        padding-left: 30px;
        @include r(1279) {
            padding-left: 15px; }
        @include r(1023) {
            float: left;
            width: 100%;
            margin-bottom: 40px; }
        &:first-child {
            padding: 0 30px 0 0;
            @include r(1279) {
                padding: 0 15px 0 0; }
            @include r(1023) {
                padding: 0; } } }
    &__graph {
        font-size: 0;
        text-align: center;
        &-img {
            max-width: 100%;
            @include r(1900) {
                max-width: 80%; }
            @include r(1279) {
                max-width: 90%; }
            @include r(1023) {
                max-width: 80%;
                width: 100%; } } }
    &__user {
        max-width: 570px;
        background: #FFF;
        @include r(1900) {
            margin-right: 40px; }
        @include r(1279) {
            margin-right: 0; }
        @include r(1023) {
            max-width: 100%; }
        &-preview {
