            font-weight: 400; } }
    .slick-prev,
    .slick-next {
        padding: 10px;
        color: $bluedark;
        @include r(1023) {
            color: #ffffff;
            vertical-align: middle; }
        .icon {
            font-size: 14px;
            @include r(1023) {
                font-size: 12px; } } }
    .slick-dots {
        display: none!important;
        @include r(1023) {
            display: inline-block!important;
            font-size: 0;
            vertical-align: middle;
            padding: 0;
            list-style: none;
            li {
                display: inline-block;
                vertical-align: middle;
                position: relative;
                top: -1px; }
            button {
                width: 30px;
                height: 30px;
                &:before {
                    content: '';
                    display: inline-block;
                    width: 10px;
                    height: 10px;
                    border: 1px solid rgba(#fff,.5);
                    border-radius: 50%;
                    vertical-align: middle;
                    transition: all .2s;
                    @include r(1023) {
                        width: 8px;
                        height: 8px; } } }
            .slick-active {
                button {
                    &:before {
                        border-color: $bluedark;
