        padding: 0 20px;
        vertical-align: middle; }
    &__img {
        max-width: 100%; } }

.questions {
    margin-bottom: 100px;
    @include clr;
    @include r(1023) {
        margin-bottom: 70px; }
    &__list,
    &__wrap {
        float: left; }
    &__list {
        width: 30%;
        padding-right: 5%;
        @include r(1900) {
            width: 40%; }
        @include r(1279) {
            width: 48%;
            padding-right: 3%; }
        @include r(1023) {
            width: 100%;
            padding-right: 0; } }
    &__wrap {
        width: 70%;
        padding: 50px;
        background-color: white;
        box-shadow: 0 3px 50.5px 0.5px rgba(23, 26, 31, 0.07);
        @include r(1900) {
            width: 60%; }
        @include r(1279) {
            width: 52%; }
        @include r(1023) {
            float: none;
            width: auto;
            height: 100%; } }
    &__item {
        @include r(1023) {
            display: inline-block;
            width: 50%;
            margin-right: -4px; } }
    &__btn {
        display: block;
